export const getName = (team: number | string, idx: number) => {
  return MemberData[team][idx - 1];
};

export const getAllScore = () => {
  const allScope: Record<
    string,
    { score: number; matchCount: number; allMatchCount: number }
  > = {};
  ScoreData.forEach((score) => {
    if (!allScope[score.teams[0]]) {
      allScope[score.teams[0]] = { score: 0, matchCount: 0, allMatchCount: 0 };
    }
    if (!allScope[score.teams[1]]) {
      allScope[score.teams[1]] = { score: 0, matchCount: 0, allMatchCount: 0 };
    }

    allScope[score.teams[0]].matchCount += 1;
    allScope[score.teams[1]].matchCount += 1;

    score.match.forEach((m) => {
      if (m.status === "unMatch") return;

      allScope[score.teams[0]].score += m.score![0];
      allScope[score.teams[1]].score += m.score![1];
      allScope[score.teams[0]].allMatchCount += 1;
      allScope[score.teams[1]].allMatchCount += 1;
    });
  });

  // 领导加分
  allScope[7].score += 30;

  return allScope;
};

export const getPersonalRank = () => {
  const personalMap = ScoreData.reduce<
    Record<string, { scope: number; victory: number; defeat: number }>
  >((prev, cur) => {
    const teamA = cur.teams[0];
    const teamB = cur.teams[1];

    cur.match.forEach((m) => {
      if (m.status === "unMatch") return;

      const aName = getName(teamA, m.group[0]);
      const bName = getName(teamA, m.group[1]);
      const cName = getName(teamB, m.group[0]);
      const dName = getName(teamB, m.group[1]);
      if (!prev[aName]) {
        prev[aName] = { scope: 0, victory: 0, defeat: 0 };
      }
      if (!prev[bName]) {
        prev[bName] = { scope: 0, victory: 0, defeat: 0 };
      }
      if (!prev[cName]) {
        prev[cName] = { scope: 0, victory: 0, defeat: 0 };
      }
      if (!prev[dName]) {
        prev[dName] = { scope: 0, victory: 0, defeat: 0 };
      }

      prev[aName].scope += m.score![0];
      prev[bName].scope += m.score![0];
      prev[cName].scope += m.score![1];
      prev[dName].scope += m.score![1];
      if (m.score![0] > m.score![1]) {
        prev[aName].victory += 1;
        prev[bName].victory += 1;
        prev[cName].defeat += 1;
        prev[dName].defeat += 1;
      } else {
        prev[aName].defeat += 1;
        prev[bName].defeat += 1;
        prev[cName].victory += 1;
        prev[dName].victory += 1;
      }
    });
    return prev;
  }, {});

  // 领导加分
  personalMap["王总"].scope += 30;

  let idx = 1;

  return Object.entries(personalMap)
    .sort((a, b) => {
      return b[1].scope - a[1].scope;
    })
    .reduce<
      Record<
        number,
        {
          name: string;
          scope: number;
          victory: number;
          defeat: number;
          allMatch: number;
          winProbability: number;
          average: number;
        }[]
      >
    >((prev, cur) => {
      const allMatch = cur[1].victory + cur[1].defeat;

      if (
        !prev[idx] ||
        prev[idx][prev[idx].length - 1].scope !== cur[1].scope
      ) {
        idx++;
      }
      (prev[idx] || (prev[idx] = [])).push({
        ...cur[1],
        name: cur[0],
        allMatch,
        average: cur[1].scope / allMatch,
        winProbability: cur[1].victory / allMatch,
      });
      return prev;
    }, {});
};
