import styled from "styled-components";
import {
  ScoreData,
  MemberData,
  ScoreDataType,
  getName,
} from "../../data/score";
import React from "react";

const HomePageStyled = styled.div`
  padding-top: 12px;
  padding-bottom: 40px;

  @media (min-width: 824px) {
    .match-list {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -16px;
    }
  }

  @media (max-width: 824px) {
    .match-list {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
`;
const MatchItemStyled = styled.div`
  width: 390px;
  margin: 16px;

  @media (max-width: 420px) {
    width: 380px;
    margin: 12px auto;
  }

  @media (max-width: 390px) {
    width: 360px;
    margin: 12px auto;
  }

  @media (max-width: 375px) {
    width: 320px;
    margin: 12px auto;
  }

  .match-date {
    color: #777;
    text-align: center;
  }

  .match-header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 12px;
    background: #eee;
    padding: 8px 0;
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
  }

  .match-team {
    display: flex;
    align-items: center;

    .team-index {
      font-size: 20px;
      font-weight: 700;
      margin-right: 8px;
    }

    .tl-name {
      font-weight: 600;
    }
    @media (max-width: 380px) {
      .team-index {
        font-size: 16px;
      }
      .tl-name {
        font-size: 14px;
      }
    }
  }
  .total-score {
    display: flex;
    align-items: center;

    .score {
      display: inline-block;
      text-align: center;
      width: 86px;
      padding: 4px 0;
      font-size: 28px;
      font-weight: 900;
      background: #2980b9;
      color: white;
      border-radius: 8px;

      &.victory {
        background: #d35400;
      }

      &.defeat {
        background: #2980b9;
      }
    }

    @media (max-width: 380px) {
      .score {
        font-size: 20px;
        width: 66px;
      }
    }

    .colon {
      font-size: 32px;
      margin: 0 8px;
      font-weight: 700;
      transform: translateY(-3px);
    }
  }

  .match-content {
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;

    .match-item-row + .match-item-row {
      border-top: 1px solid #ccc;
    }

    .match-item-row {
      display: flex;
      padding: 6px 0;
      align-items: center;

      .team-members {
        width: 130px;
        text-align: center;
        font-size: 14px;
        color: #555;
      }
      .match-score {
        width: 130px;
        text-align: center;

        .score {
          font-size: 20px;
          font-weight: 600;
        }
        .colon {
          font-size: 20px;
          margin: 0 6px;
          font-weight: 700;
          transform: translateY(-1px);
          display: inline-block;
        }
      }
    }
  }
`;

const MatchItem: React.FC<{ item: ScoreDataType }> = (props) => {
  const { item } = props;

  const teamA = item.teams[0];
  const teamB = item.teams[1];
  const total = item.match.reduce<number[]>((p, c) => {
    if (c.status === "unMatch") return p;
    c.score?.forEach((s, idx) => {
      p[idx] = (p[idx] || 0) + s;
    });
    return p;
  }, []);

  return (
    <MatchItemStyled>
      <div className="match-date">{item.date}</div>
      <div className="match-header">
        <div className="match-team team-a">
          <span className="team-index">{teamA}</span>
          <span className="tl-name">{MemberData[teamA][0]}</span>
        </div>
        <div className="total-score">
          <span
            className={`score ${total[0] < total[1] ? "defeat" : "victory"}`}
          >
            {total[0]}
          </span>
          <span className="colon">:</span>
          <span
            className={`score ${total[1] < total[0] ? "defeat" : "victory"}`}
          >
            {total[1]}
          </span>
        </div>
        <div className="match-team team-b">
          <span className="team-index">{teamB}</span>
          <span className="tl-name">{MemberData[teamB][0]}</span>
        </div>
      </div>
      <div className="match-content">
        {item.match.map((m, idx) => {
          return (
            <div className="match-item-row" key={idx}>
              <div className="team-members">
                {`${getName(teamA, m.group[0])} / ${getName(
                  teamA,
                  m.group[1]
                )}`}
              </div>
              <div className="match-score">
                {m.status === "unMatch" ? (
                  <>未进行</>
                ) : (
                  <>
                    <span className="score">{m.score![0]}</span>
                    <span className="colon">:</span>
                    <span className="score">{m.score![1]}</span>
                  </>
                )}
              </div>
              <div className="team-members">
                {`${getName(teamB, m.group[0])} / ${getName(
                  teamB,
                  m.group[1]
                )}`}
              </div>
            </div>
          );
        })}
      </div>
    </MatchItemStyled>
  );
};

const HomePage = () => {
  return (
    <HomePageStyled>
      <div className="match-list">
        {ScoreData.map((item) => (
          <MatchItem item={item} key={item.date + item.teams} />
        ))}
      </div>
    </HomePageStyled>
  );
};

export default HomePage;
