import { useLayoutEffect, useRef, useState } from "react";
import styled from "styled-components";
import { getPersonalRank } from "../../data/score";

const PersonalRankStyled = styled.div`
  margin: 0 auto;
  padding-top: 20px;
  width: 600px;
  padding-bottom: 40px;

  @media (max-width: 420px) {
    width: 100%;
  }

  .personal-item + .personal-item {
    margin-top: 8px;
  }
  .personal-item {
    border: 1px solid #2980b9;
    padding: 8px 12px;
    display: flex;
    align-items: center;

    border-radius: 4px;

    .personal-idx {
      font-weight: 900;
      width: 48px;
    }

    .personal-name {
      width: 80px;
    }

    .personal-scope {
      background: #2980b9;
    }
    .personal-victory,
    .personal-defeat,
    .personal-scope,
    .personal-average,
    .personal-winProbability {
      padding: 0 8px;
      border-radius: 4px;
      color: white;
      font-size: 14px;
      display: flex;
      align-items: center;
    }

    .personal-victory {
      margin-left: 20px;
      background: #d35400;
    }

    .personal-defeat {
      margin-left: 20px;
      background: #27ae60;
    }
    .personal-average {
      margin-left: 20px;
      background: #8e44ad;
    }
    .personal-winProbability {
      margin-left: 20px;
      background: #e58e26;
    }

    @media (max-width: 420px) {
      .personal-victory,
      .personal-defeat,
      .personal-average,
      .personal-winProbability {
        margin-left: 12px;
      }
    }
  }
  &.is-show-more {
    .personal-item {
      @media (max-width: 500px) {
        padding: 8px 6px;
        font-size: 13px;

        .personal-idx {
          font-weight: 900;
          width: 28px;
        }

        .personal-victory,
        .personal-defeat,
        .personal-average,
        .personal-winProbability {
          margin-left: 6px;
          padding: 0 4px;
          font-size: 13px;
        }
        .personal-scope {
          font-size: 13px;
          padding: 0 3px;
        }
      }
    }
  }
`;

const PersonalRank = () => {
  const personalRank = getPersonalRank();
  const [showMore, setShowMore] = useState(
    localStorage.getItem("personal-show-more") === "true"
  );

  const showMoreCountRef = useRef(0);
  const containerRef = useRef<HTMLDivElement>(null);

  console.log("showMore--", showMore);
  console.log("containerRef class--", containerRef.current?.className);

  useLayoutEffect(() => {
    if (showMore) {
      containerRef.current?.classList.add("is-show-more");
    } else {
      containerRef.current?.classList.remove("is-show-more");
    }
  }, [showMore]);

  return (
    <PersonalRankStyled
      className={showMore ? "is-show-more" : ""}
      ref={containerRef}
    >
      {Object.values(personalRank).map((r, idx) => {
        return r.map((personal) => (
          <div
            className="personal-item"
            key={personal.name}
            onClick={() => {
              if (idx === 0) {
                if (++showMoreCountRef.current === 10) {
                  showMoreCountRef.current = 0;
                  localStorage.setItem(
                    "personal-show-more",
                    showMore ? "false" : "true"
                  );
                  setShowMore(!showMore);
                }
              }
            }}
          >
            <span className="personal-idx">
              {idx < 9 ? `0${idx + 1}` : idx + 1}
            </span>
            <span className="personal-name">{`${personal.name} `}</span>
            <span
              className="personal-scope"
              title={`${(personal.winProbability * 100).toFixed(2)}%`}
            >
              分:{personal.scope}
            </span>
            <span className="personal-victory">胜:{personal.victory}</span>
            <span className="personal-defeat">败:{personal.defeat}</span>
            {showMore && (
              <>
                <span className="personal-average">
                  均:{personal.average.toFixed(2)}
                </span>
                <span className="personal-winProbability">
                  率:{`${(personal.winProbability * 100).toFixed(0)}%`}
                </span>
              </>
            )}
          </div>
        ));
      })}
    </PersonalRankStyled>
  );
};

export default PersonalRank;
