import React from "react";
import { Routes, Route } from "react-router-dom";
import MainHeader from "./component/MainHeader";

import Home from "./pages/home";
import Rank from "./pages/rank";
import PersonalRank from "./pages/personalRank";

function App() {
  return (
    <div className="App">
      <MainHeader />
      <main className="app-main">
        <Routes>
          <Route
            path={process.env.NODE_ENV === "development" ? "/" : "/wuyulunbi"}
            element={<Home />}
          />
          <Route
            path={
              process.env.NODE_ENV === "development"
                ? "/rank"
                : "/wuyulunbi/rank"
            }
            element={<Rank />}
          />
          <Route
            path={
              process.env.NODE_ENV === "development"
                ? "/personalRank"
                : "/wuyulunbi/personalRank"
            }
            element={<PersonalRank />}
          />
        </Routes>
      </main>
    </div>
  );
}

export default App;
