import { NavLink, useLocation } from "react-router-dom";
import styled from "styled-components";

const MainHeaderStyled = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 8px 8px;
  height: 50px;
  position: sticky;
  top: 0;
  background: white;
  z-index: 99;

  .logo {
    font-size: 24px;
    font-weight: 900;
    margin-right: 32px;
  }
  .navigator {
    a + a {
      margin-left: 16px;
    }

    a.active {
      color: #2980b9;
    }

    a:hover {
      opacity: 0.7;
    }
  }
`;

const MainHeader = () => {
  const location = useLocation();

  return (
    <MainHeaderStyled>
      <div className="logo">午羽轮比</div>
      <div className="navigator">
        <NavLink
          to={process.env.NODE_ENV === "development" ? "/" : "/wuyulunbi"}
          className={() =>
            location.pathname.includes("personalRank") ||
            location.pathname.includes("rank")
              ? ""
              : "active"
          }
        >
          赛况
        </NavLink>
        <NavLink
          to={
            process.env.NODE_ENV === "development" ? "/rank" : "/wuyulunbi/rank"
          }
          className={({ isActive }) => (isActive ? "active" : "")}
        >
          排行
        </NavLink>
        <NavLink
          to={
            process.env.NODE_ENV === "development"
              ? "/personalRank"
              : "/wuyulunbi/personalRank"
          }
          className={({ isActive }) => (isActive ? "active" : "")}
        >
          个人排行
        </NavLink>
      </div>
    </MainHeaderStyled>
  );
};

export default MainHeader;
