import React from "react";
import { render, hydrate } from "react-dom";
import "./index.css";
import { BrowserRouter } from "react-router-dom";
import App from "./App";

const root = document.getElementById("root") as HTMLElement;

if (root.hasChildNodes()) {
  hydrate(
    <React.StrictMode>
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </React.StrictMode>,
    root
  );
} else {
  render(
    <React.StrictMode>
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </React.StrictMode>,
    root
  );
}
