import { useRef, useState } from "react";
import styled from "styled-components";
import { getAllScore, getName } from "../../data/score";

const RankPageStyled = styled.div`
  width: 600px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 40px;

  &.is-show-more {
    @media (max-width: 600px) {
      .rank-item-row2 {
        margin-left: 0 !important;
        .tag-item {
          padding: 0 8px !important;
          font-size: 14px !important;
        }
        .tag-item + .tag-item {
          margin-left: 10px !important;
        }
      }
    }
  }

  @media (max-width: 600px) {
    width: 100%;
    padding-top: 20px;
  }

  .rank-item + .rank-item {
    margin-top: 20px;
  }
  .rank-item {
    border: 1px solid #2980b9;
    padding: 12px 16px;
    border-radius: 8px;

    .rank-item-row1 {
      display: flex;
      align-items: flex-end;

      .rank-idx {
        font-size: 20px;
        font-weight: 800;
        width: 40px;
      }
      .team-idx-name {
        display: inline-flex;
        align-items: center;
        .tl-name {
          width: 50px;
        }
      }
    }
    .rank-item-row2 {
      display: flex;
      align-items: center;
      margin-top: 8px;
      margin-left: 40px;

      .tag-item {
        padding: 0px 12px;
        border-radius: 4px;
        color: white;
      }
      .tag-item + .tag-item {
        margin-left: 12px;
      }

      .rank-score {
        background: #2980b9;
      }
      .rank-match-count {
        background: #d35400;
      }
      .rank-all-match-count {
        background: #27ae60;
      }
      .rank-all-average {
        background: #8e44ad;
      }
    }
  }
`;

const RankPage = () => {
  const score = getAllScore();
  const [showMore, setShowMore] = useState(
    localStorage.getItem("personal-show-more") === "true"
  );

  const showMoreCountRef = useRef(0);

  return (
    <RankPageStyled className={showMore ? "is-show-more" : ""}>
      <div>
        {Object.entries(score)
          .sort((a, b) => b[1].score - a[1].score)
          .map((r, idx) => {
            return (
              <div
                className="rank-item"
                key={idx}
                onClick={() => {
                  if (idx === 0) {
                    if (++showMoreCountRef.current === 10) {
                      showMoreCountRef.current = 0;
                      localStorage.setItem(
                        "personal-show-more",
                        showMore ? "false" : "true"
                      );
                      setShowMore(!showMore);
                    }
                  }
                }}
              >
                <div className="rank-item-row1">
                  <span className="rank-idx">0{idx + 1}</span>
                  <span className="team-idx-name">
                    <span className="tl-name">{getName(r[0], 1)}</span>
                    <span className="team-idx">{`（${r[0]} 队）`}</span>
                  </span>
                </div>
                <div className="rank-item-row2">
                  <span className="rank-score tag-item">总分:{r[1].score}</span>
                  <span className="rank-match-count tag-item">
                    轮数:{r[1].matchCount}
                  </span>
                  <span className="rank-all-match-count tag-item">
                    场数:{r[1].allMatchCount}
                  </span>
                  {showMore && (
                    <span className="rank-all-average tag-item">
                      场均:{(r[1].score / r[1].allMatchCount).toFixed(2)}
                    </span>
                  )}
                </div>
              </div>
            );
          })}
      </div>
    </RankPageStyled>
  );
};

export default RankPage;
